import { Component } from '@angular/core';
import { Finance } from 'financejs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {
  loanAmt: String;
  interest: String;
  tenure: String;
  emi: String = null;
  irr: String = null;
  grossIrr: String = null;
  finance = new Finance();

  onCalculate() {
    const vm = this;
    vm.calculateEMI();
  }

  calculateEMI() {
    const vm = this;
    vm.emi = '0';
    if (+vm.loanAmt && +vm.interest && +vm.tenure) {
      const emi = vm.finance.AM(+vm.loanAmt, +vm.interest, +vm.tenure, 1);
      vm.emi = emi.toFixed(2).toString();
    }
    vm.calculateGrossIrr();
  }

  calculateGrossIrr() {
    const vm = this;
    vm.irr = '0';
    vm.grossIrr = '0';
    if (+vm.loanAmt && +vm.emi && +vm.tenure) {
      const initialInvestment = -(+vm.loanAmt);
      const dataAry = [];
      dataAry[0] = initialInvestment;
      for (let i = 0; i < +vm.tenure; ++i) {
        dataAry.push(+vm.emi);
      }
      const irr = vm.finance.IRR.apply(null, dataAry);
      vm.irr = irr.toFixed(2).toString();
      const calIrr = (Math.pow( 1 + (irr / 100), 12) - 1) * 100;
      vm.grossIrr = calIrr.toFixed(2).toString();
    }
  }
}



